$activityIndicatorSize: 32px;

.iconContainer {
  display: flex;
}

.activityIndicator {
  composes: spinner from 'app/js/views/v3/components/core/base/Spinner.css';

  position: absolute;
  top: calc($activityIndicatorSize - $navigationIconSize);
  left: calc($activityIndicatorSize - $navigationIconSize);
  width: $activityIndicatorSize;
  height: $activityIndicatorSize;
}
