.container {
  @add-mixin resetFont;
  position: relative;
  z-index: $navbarZIndex;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  flex-grow: 1;
  margin: 8px;
  height: 48px;
  border-radius: 4px;
  background: var(--color-surface-background-60);
}

:global(.show-video-player) .container {
  opacity: 0;
  pointer-events: none;
}

.side {
  display: flex;
  align-items: center;
  height: 100%;
}

.plexLogo {
  composes: button from 'app/js/views/v3/components/navbar/NavBarIconButton.css';

  width: unset;
  font-size: 16px;
}

.buttonSeparator {
  margin: 8px;
  width: 2px;
  height: 66%;
  background-color: $menuSeparatorBackgroundColor;
}

.accountButton {
  composes: button from './NavBarAccountButton.css';

  display: inline-flex;
  align-items: center;
  flex-shrink: 0;
  margin-left: 25px;
}

.left {
  composes: side;

  padding-left: 8px;
}

.right {
  composes: side;

  padding-right: 17px;
}
