.button {
  composes: button from 'app/js/views/v3/components/core/buttons/IconButton.css';

  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 var(--size-xs);
  width: var(--size-xxl);
  height: 100%;
  font-size: $navigationIconSize;

  &:global(.isDisabled) {
    color: $dimColor;
  }
}

.tooltip {
  composes: bottomCenter from 'app/js/views/v3/components/core/tooltips/Tooltip.css';

  margin-top: $tooltipMarginTop;
}

.tooltipTriggerHasFocus {
  composes: tooltip;

  margin-top: calc($tooltipMarginTop * -1);
}
