//
// Cards
// --------------------------------------------------


// Base
// ------------------------

@card-bg: rgba(0, 0, 0, 0.15);
@card-hover-bg: rgba(255, 255, 255, 0.08);

.card {
  position: relative;
  padding: 10px;
  background-color: @card-bg;
}

a.card:hover,
a.card:focus {
  background-color: @card-hover-bg;
}

.card-poster {
  float: left;
  margin-right: 15px;
}

.card-actions {
  float: right;
  margin-right: -4px;
  padding-left: 10px;
  font-size: 0;
}

.card-action-btn {
  .btn-gray;
  padding: 0 8px;
  font-size: @font-size-large;

  &.btn-success,
  &.btn-danger {
    background: none;

    &.disabled {
      .opacity(0.2);
    }
  }

  &.btn-success.disabled {
    color: @brand-success;
  }

  &.btn-danger.disabled {
    color: @brand-danger;
  }
}

.card-details {
  clear: right;
  overflow: hidden;
}


// Sortable
// ------------------------

@card-sortable-height: 36px;
@card-sortable-line-height: @card-sortable-height - 2px;

.card-sortable {
  padding: 0 20px 0 0;
  height: @card-sortable-height;
  border-bottom: 1px solid darken(@card-bg, 2%);
  line-height: @card-sortable-line-height;
  .cursor(grab);
  .user-select(none);

  &:hover,
  &:focus {
    background-color: @card-hover-bg;

    .card-handle {
      background-color: lighten(@card-hover-bg, 1%);
      color: rgba(255, 255, 255, 0.7);
    }
  }

  .card-actions {
    margin-right: -8px;
    line-height: 0;
  }

  .card-action-btn {
    line-height: @card-sortable-line-height;
  }

  label {
    margin: 0;
  }
}

.card-handle {
  display: inline-block;
  margin-right: 10px;
  width: 26px;
  background-color: lighten(@card-bg, 1%);
  color: rgba(255, 255, 255, 0.3);
  text-align: center;
  font-size: @font-size-small;
}


// List
// ------------------------

.card-list {
  background-color: @card-bg;
}


// Tile List
// ------------------------

.card-tile-list {
  margin: 0 -20px 0 0;
  .clearfix();
}

.card-tile-list-single-item,
.card-tile-list-item {
  padding: 0 20px 20px 0;
  .clearfix();

  > .card {
    display: inline-block;
    width: 100%;
  }
}

.card-tile-list-single-item,
.card-2-col-item {
  h4 {
    margin: 0;
    color: @gray-lightest;
    .text-overflow();
  }

  .card-actions > .btn-gray {
    font-size: @font-size-large;
  }
}

/* Small devices (tablets, 768px and up) */
@media (min-width: @screen-sm) {
  .card-tile-list-item {
    float: left;
    width: 50%;

    &:nth-child(odd) {
      clear: both;
    }
  }

  .card-2-col-item {
    float: none;
    width: auto;
  }
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: @screen-md) {
  // Keep .card-tile-list-item for the specificity override on large devices.
  .card-tile-list-item.card-2-col-item {
    float: left;
    width: 50%;

    &:nth-child(3n + 1) {
      clear: none;
    }

    &:nth-child(odd) {
      clear: both;
    }
  }
}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: @screen-lg) {
  .card-tile-list-item {
    width: 33.33%;

    &:nth-child(3n + 1) {
      clear: both;
    }

    &:nth-child(odd) {
      clear: none;
    }
  }
}


// Sections
// ------------------------

.card-section {
  padding: 10px 20px;

  .section-icon {
    margin-top: 2px;
    margin-right: 5px;
    color: @gray-lighter;
  }

  .section-path, .section-count {
    display: block;
    margin-left: 24px;
    color: @gray-light;
  }

  .card-actions  > .btn-gray:last-child {
    padding-right: 0;
  }
}


// Devices
// ------------------------

.card-device {
  .product {
    color: @gray-lighter;
  }

  .version,
  .device,
  .platform-info,
  .last-seen {
    color: rgba(255, 255, 255, 0.45);
  }

  .last-seen {
    text-align: right;
  }
}
