//
// Filter Bar
// --------------------------------------------------


@filterbar-height: 50px;
@filterbar-padding-top: 13px;
@filterbar-line-height: (@filterbar-height - 2) - @filterbar-padding-top * 2;

.filter-bar {
  .card;
  height: @filterbar-height;
  line-height: @filterbar-line-height;
  padding: @filterbar-padding-top 20px;
  margin: 20px 0;
  .clearfix();

  h4 {
    position: relative;
    left: 15%;
    width: 70%;
    margin: 0;
    font-family: @font-family-bold;
    line-height: @filterbar-line-height;
    color: @gray-light;
    text-align: center;
    text-transform: uppercase;
    .text-overflow();
  }
}

.filter-bar-fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  margin: 0;
  border-radius: 0;
}

.filter-bar-right {
  float: right;
  font-size: 0;

  > .btn {
    margin-left: 10px;
  }

  > .btn-gray {
    margin-left: 20px;
    font-size: @font-size-large + 4;
  }
}
