.button {
  composes: button from 'app/js/views/v3/components/navbar/NavBarIconButton.css';
}

.activityIcon {
  composes: iconContainer from './NavBarActivityIcon.css';

  transition: color $defaultSpeed;
}

.label {
  position: absolute;
  right: 44px;
  font-size: $smallFontSize;
  font-family: $boldFontFamily;
}

.isHighlighted {
  .activityIcon {
    color: $primaryAlternateColor;
  }

  .label {
    color: $primaryAlternateColor;
  }
}

.isHighlighted:hover {
  .activityIcon {
    color: lighten($primaryAlternateColor, 0.1);
  }

  .label {
    color: lighten($primaryAlternateColor, 0.1);
  }
}
